import React from "react";
import {Text} from "informed";
import styles from "./style.module.css";

const HEIGHT = 35;

export default class Input extends React.Component {
  state = {
    placeholder: true,
  };

  onFocus = (e) => this.setState({placeholder: false});
  onBlur = (e) => this.setState({placeholder: !e.target.value});

  componentDidMount() {
    this.input = this.refs.container.querySelector('input');
    this.input.addEventListener('focus', this.onFocus);
    this.input.addEventListener('blur', this.onBlur);
  }

  render() {
    const otherProps = Object.assign({}, this.props);
    delete otherProps.placeholder;

    return (
      <div ref={"container"} className={styles.container} style={{height: HEIGHT, borderRadius: HEIGHT/2}}>
        <div className={styles.placeholder} style={{height: HEIGHT}}>
          {this.state.placeholder ? this.props.placeholder : null}
        </div>
        <Text className={styles.input} autoComplete={"off"} style={{height: HEIGHT}} {...otherProps}/>
      </div>
    );
  }
};