import React, {Component} from 'react';
import {Link} from "react-router-dom";
import styles from './style.module.css';
import Container from "react-bootstrap/Container";
import { Form, Text } from 'informed';
import Image from "react-bootstrap/Image";

import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import Wrapper from "../common/core/Wrapper";
import Input from "../common/core/Input";
import Checkbox from "../common/core/Checkbox";
import SignFooter from "../common/parts/SignFooter";

import logoImagePath from './images/logo.png';
import fbImagePath from './images/fb.png';
import glImagePath from './images/gl.png';
import twImagePath from './images/tw.png';
import loginImagePath from './images/login.png';
import passwordImagePath from './images/password.png';

const WIDTH = 350;

//DEV
const URL = process.env.PUBLIC_URL;

class SignIn extends Component {
  onSubmit = (user) => {
    console.log(user);
  };

  render() {
    return (
      <Wrapper className={styles.wrapper}>

        <Container className={styles.main} style={{width: WIDTH}}>
          <Row>
            <Image className={styles.logoImage} src={logoImagePath}/>
          </Row>
          <Row className={styles.tabs}>
            <Col className={styles.tabSignIn}>Sign In</Col>
            <Col className={styles.tabSignUp}>
              <Link to={URL + "/register"}>Sign Up</Link>
            </Col>
          </Row>

          <Form onSubmit={this.onSubmit} className={styles.form}>

            <Row className={styles.loginContainer}>
              <Input
                field={"login"}
                placeholder={<Placeholder imageSrc={loginImagePath} text={"Login"}/>}
              />
            </Row>

            <Row className={styles.passwordContainer}>
              <Input
                field={"password"}
                type={"password"}
                placeholder={<Placeholder imageSrc={passwordImagePath} text={"Password"}/>}
              />
            </Row>

            <button type={"submit"} hidden/>

            <Row>
              <Col className={styles.rememberContainer}>
                <Checkbox field={"remember"}/>
                <span className={styles.rememberText}>Remember me</span>
              </Col>
              <Col className={styles.forgotText}>
                <Link to={""}>Forgot Login/Password</Link>
              </Col>
            </Row>
          </Form>

          <Row className={styles.signInWithContainer}><Col>sign in with</Col></Row>
          <Row className={styles.servicesContainer}>
            <SignByLink href={""} imageSrc={fbImagePath}/>
            <SignByLink href={""} imageSrc={glImagePath}/>
            <SignByLink href={""} imageSrc={twImagePath}/>
          </Row>
        </Container>

        <SignFooter className={styles.footer}/>

      </Wrapper>
    );
  }
}

const SignByLink = ({href, imageSrc}) => {
  return (
    <Col className="text-center">
      <Link to={href}><Image height={40} src={imageSrc}/></Link>
    </Col>
  );
};

const Placeholder = ({text, imageSrc}) => {
  return (
    <div className={styles.placeholderContainer}>
      <Image className={styles.placeholderImage} src={imageSrc}/>
      <span className={styles.placeholderText}>{text}</span>
    </div>
  );
};

export default SignIn;