import React, {Component} from 'react';
import styles from './style.module.css';
import Col from "react-bootstrap/Col";
import cn from 'classnames';
import Row from "react-bootstrap/Row";
import Container from "react-bootstrap/Container";

const WIDTH = 700;
const BAR_UNIT_WIDTH = 70;

const STEPS = ['Profile', 'Company', 'Business'];

const PROFILE = 0;
const COMPANY = 1;
const BUSINESS = 2;

class SignUp extends Component {
  state = {
    step: PROFILE,
  };

  nextStep = () => {
    this.setState({step: this.state.step+1});
  };

  stepContent = (step) => {
    switch (step) {
      case (PROFILE): return "1";
      case (COMPANY): return "2";
      case (BUSINESS): return "3";
    }
  };

  render() {
    return (
      <Container style={{width: WIDTH}}>
        <ProgressBar step={this.state.step}/>
        {this.stepContent(this.state.step)}
        <button onClick={this.nextStep}>next</button>
      </Container>
    );
  }
}

const ProgressBar = ({step}) => (
    <Row className={styles.barContainer}>
    <Row className={styles.barLine}>{}</Row>
    {STEPS.map((text, i) => <Unit text={text} complete={i < step}/>)}
    </Row>
);

const Unit = ({text, complete}) => (
  <div className={styles.barUnitContainer} style={{width: BAR_UNIT_WIDTH}}>
    <div className={cn(styles.barUnitRound, complete && styles.complete)}></div>
    <div>{text}</div>
  </div>
);

export default SignUp;