import React from "react";
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import {Link} from "react-router-dom";
import styles from './style.module.css';

export default ({className}) => {
  return (
    <div className={`${className ? className : ''} ${styles.container}`}>
      <Row>
        <Col className="text-right pr-2">
          <Link to={""}><span className={styles.link}>Solution</span></Link>
        </Col>
        <Col className="pl-2">
          <Link to={""}><span className={styles.link}>About us</span></Link>
        </Col>
      </Row>
      <Row><Col className={`${styles.copyright} text-center`}>Copyright 2019. All rights Reserved</Col></Row>
    </div>
  );
}